*{
  box-sizing:border-box;
}

html{
  width:100%;
  height:100%;
  background-color:rgb(39, 39, 65);
}

#root{
  height:inherit;
}

body {
  position: relative;
    width: 98%;
    height: 96%;
    margin: 0.8em auto;
    background-color:rgb(20, 19, 34);    
    border-radius:0.5em;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hot-cold-game
{
    text-align:center;
    color:#fff;
}

.hot-cold-game h1{
    margin-top:0;
    clear:both;
    font-size:3em;
}

.hot-cold-game nav{
    text-align:left;
    height:30px;
    margin:5px;
    margin-bottom:80px;
}

nav ul{
    padding:10px;
    list-style: none;
}


nav li{
    text-decoration: none;
    display:inline;
    font-size:1.2em;
}

.new-game-link{
    float:right;
}

.game-container{
    height:400px;
    width:400px;
    border:1px solid #222;
    background-color:rgb(63, 58, 80);
    border-radius: 0.5em;
    margin:0 auto;
    overflow:hidden;
}

.link-button
{
    background:none;
    border:none;
    color:white;
    font-size:1.2em;
}

.link-button:hover{
    text-decoration:underline;
    cursor: pointer;
}
.game-top
{
    background-color:red;
    padding:20px;
    height:75px;
}

.game-top h2
{
    margin:0;
    font-weight:normal;
}
.game-input
{
    height:250px;
    padding-top:30px;
    padding-bottom:20px;
    padding-left:40px;
    padding-right:40px;
}

.game-input input{
    display:block;
    border-radius:0.2em;
    width:100%;
    font-size:35px;
    text-align:center;
    border:1px solid black;
    color:white;
    background-color:rgb(80, 76, 94);
}

.game-input button{
    border:1px solid black;
    background-color:#333;
    color:white;
    width: 100%;
    font-size:35px;
    border-radius:0.2em;
    margin-top:20px;
}

.game-input button:hover{
    background-color:rgb(51, 109, 51);
    border:1px solid white;
}

.game-input p{
    font-size:30px;
    color:#ccc;
}

.game-input input:focus,
.game-input select:focus,
.game-input textarea:focus,
.game-input button:focus {
    outline: none;
}

.guess-count
{
    color:goldenrod;
    font-weight:bold;
}

.game-input .new-game-prompt
{
    font-size:20px;
}
.game-guesses
{
    background-color:green;
    height:73px;
    overflow-x:scroll;
}

.game-guesses ul{
    padding:0;
    list-style: none;
}

.game-guesses li{
    display:inline;
    background-color:rgba(75, 75, 75, 0.74);
    border:1px solid white;
    margin-left:5px;
    margin-right:5px;
    padding:5px;
    border-radius:0.1em;
}
